import {CONFIG_HEADER, CONFIG_HEADER_FILE} from './urls'

export default {
    setDevice(device) {

    },
    getSession() {
        let info = {};
        if (sessionStorage.getItem('memberInfo') && sessionStorage.getItem('accessToken')) {
            const memberInfo = JSON.parse(sessionStorage.getItem('memberInfo'))
            const token = sessionStorage.getItem('accessToken')
            if (memberInfo?.id && token) {
                info.memberInfo = memberInfo;
                info.token = token;
            }
        }
        return info;
    },
    setSession(params) {

        sessionStorage.setItem('memberInfo', JSON.stringify(params.memberInfo))
        sessionStorage.setItem('accessToken', params.access_token)
        CONFIG_HEADER.Authorization = 'Bearer ' + params.access_token;
        CONFIG_HEADER_FILE.Authorization = 'Bearer ' + params.access_token;


    },
    updateSession(params) {
        sessionStorage.setItem('memberInfo', JSON.stringify(params))

    },
    logout() {
        sessionStorage.setItem('memberInfo', '')
        sessionStorage.setItem('accessToken', '')
        CONFIG_HEADER.Authorization = '';
        CONFIG_HEADER_FILE.Authorization = '';
    }

}
