import axios from 'axios'
import {
    CONFIG_HEADER,
    CONFIG_HEADER_FILE
} from './urls'
import AUTH from './auth'

export default {
    DEBGU: true,
    imgHost:'/',
    post(url, frm, afterFunc, errorFunc) {
        this.checkDebug();
        this.checkBearerToken();
        axios.post(url, frm, {headers: CONFIG_HEADER}).then(res => {
            if (this.DEBGU) {
                console.log(res)
            }
            switch (res.data.status) {
                case 'success':
                case 'message':
                    afterFunc(res.data);
                    break;
                case 'notLogin':
                    AUTH.logout();
                    location.href='/';
                    break;
                case 'error':
                    alert(' ');
                    break;
                case 'fail':
                    alert(' ');
                    break;
            }

        }).catch((error) => {
            this.setError(error);

        });
    },
    async asyncPost(url, frm) {
        this.checkBearerToken();
        return await axios.post(url,frm,{headers: CONFIG_HEADER});
    },
    postFile(url, frm, afterFunc) {
        this.checkDebug();
        this.checkBearerToken();
        axios.post(url, frm, {headers: CONFIG_HEADER_FILE}).then(res => {
            if (this.DEBGU) {
                console.log(res.data)
            }
            switch (res.data.status) {
                case 'success':
                case 'message':
                    afterFunc(res.data);
                    break;
                case 'notLogin':
                    AUTH.logout();
                    location.href='/';
                    break;
                case 'error':
                    alert(' ');
                    break;
                case 'fail':
                    alert(' ');
                    break;
            }
        }).catch((error) => {
            this.setError(error);

        });
    },
    get(url, frm, afterFunc, errorFunc) {
        this.checkDebug();
        this.checkBearerToken();
        axios.get(url, {params: frm, headers: CONFIG_HEADER}).then(res => {
            if (this.DEBGU) {
                console.log(res.data)
            }
            switch (res.data.status) {
                case 'success':
                case 'message':
                    afterFunc(res.data);
                    break;
                case 'notLogin':
                    AUTH.logout();
                    location.href='/';
                    break;
                case 'error':
                    alert(' ');
                    location.href='/';
                    break;
                case 'fail':
                    alert(' ');
                    location.href='/';
                    break;
            }


        }).catch((error) => {
            this.setError(error);
            if (errorFunc) errorFunc(error.response.data)



        });
    },
    checkDebug() {
        if (location.hostname == 'localhost') {
            this.DEBGU = true;
            this.imgHost = 'http://localhost/';

        } else this.DEBGU = false;
    },
    checkBearerToken() {
        const info = AUTH.getSession();
        if (info?.memberInfo) {
            const token = info.token;
            CONFIG_HEADER.Authorization = 'Bearer ' + token;
            CONFIG_HEADER_FILE.Authorization = 'Bearer ' + token;
        }
        if(!sessionStorage.getItem('sessionId')) {
            const now = new Date();
            sessionStorage.setItem('sessionId','sess_'+now.getTime())
        }
        CONFIG_HEADER.sessionId = sessionStorage.getItem('sessionId');
    },
    setError(error) {
        if (error.response) {
            switch (error.response.status) {
                case 403:
                case 500:
                    AUTH.logout();
                    location.href='/';
                    break;
                default:
                    AUTH.logout();
                    alert(' ');
                    location.href='/';
                    break;
            }
            //    2xx     .
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
        } else if (error.request) {
            //      .
            // `error.request`  XMLHttpRequest  
            // Node.js http.ClientRequest .
            console.log(error.request);
        } else {
            //       .
            console.log('Error', error.message);
        }

    }


}
